body{margin:0;padding:0}header{height:11vh;background-image:linear-gradient(to bottom, rgba(0,0,0,0.9), #000);display:flex}header img{width:20%;margin:1% 5%}.poster{height:89vh;background-image:linear-gradient(to bottom, rgba(0,0,0,0.98), rgba(0,0,0,0.4)),url(https://wallpaperstream.com/wallpapers/thumbnails/avengers-infinity-war/Avengers-Infinity-War-Full-Casts-Poster_thumb2x.jpg);background-position:center;background-size:cover;display:flex;flex-direction:column;justify-content:space-evenly}.poster__title{font-family:"Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;color:#fff;text-shadow:1px 1px #1d1d1d}.poster__overview{width:80%;font-family:"Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;color:#fff;text-shadow:1px 1px #1d1d1d}.sorting{display:flex;flex-wrap:wrap;justify-content:center;margin-top:1.2rem}.sorting__button:hover :first-child{transform:translate(0px, -5px);transition:ease-out 0.5s}.sorting__icon{font-size:1.5rem;color:white;margin:0.4rem;padding:0.2rem;border:1px solid #fff;border-radius:4px}.sorting__title{color:white}.searchbox{background:#eaeaea;border:1px solid #a8a8a8;box-shadow:6px 4px 5px #969696}.searchbox:focus{background:#dadada;border:none;outline:none;box-shadow:0 0 0 0.2rem rgba(0,123,255,0.397)}.movies{background-image:radial-gradient(circle, #d3d3d3, #fff)}.movies__title{text-align:center;text-shadow:1px 1px #1d1d1d}.movies__elements{display:grid;grid-template-columns:repeat(5, 4fr);grid-gap:40px;justify-items:center;margin-bottom:20px}.movies__elements--img{animation:AnimateGrid 0.5s;justify-self:center;border:1px solid #e1e1e1;border-radius:5%;box-shadow:2px 2px 5px #646464;cursor:pointer;transition:ease-out 0.2s}.movies__elements--img:hover{box-shadow:2px 2px 5px #292929;transform:translate(0px, -5px);transition:ease-out 0.2s}@keyframes AnimateGrid{from{opacity:0}to{opacity:1}}@media screen and (max-width: 1194px){.movies__elements{grid-template-columns:repeat(4, 1fr)}}@media screen and (max-width: 953px){.movies__elements{grid-template-columns:repeat(3, 1fr)}}@media screen and (max-width: 710px){.movies__elements{grid-template-columns:repeat(2, 1fr)}.movies__elements--img{width:160px}}.loadmore__button button{background-color:#16d47b;color:#fff}.loading__icon{width:4rem;height:4rem;animation:spinningButton 0.8s linear infinite;border:0.3rem solid #fff;border-top:0.3rem solid #16d47b;border-radius:50%;margin:1.4rem auto}@keyframes spinningButton{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.navigation{font-family:"Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;background:#555555;padding:6px 0}.navigation__link{font-size:1.2rem;color:#000;text-decoration:none}.navigation__link:hover{color:#00aeff;text-decoration:none}.navigation__span{font-size:16px;color:#fff}@keyframes animateMovieinfo{from{opacity:0}to{opacity:1}}.movie{background-size:cover;background-position:center;padding:40px 20px;box-sizing:border-box;animation:animateMovieinfo 1s}.movie__content{display:flex;background:rgba(0,0,0,0.7);border-radius:8px;margin:0 auto}.movie__content--img{display:flex;align-self:center;border-radius:2px}.movies__elements1{background-image:radial-gradient(circle, #d3d3d3, #fff);display:grid;grid-template-columns:repeat(5, 4fr);grid-gap:40px;justify-items:center;margin-bottom:20px}@media screen and (max-width: 710px){.movies__elements1{grid-template-columns:repeat(2, 1fr)}.movies__elements1--img{width:160px}.movie__content--img{display:none}}@media screen and (max-width: 953px){.movies__elements1{grid-template-columns:repeat(3, 1fr)}}@media screen and (max-width: 1194px){.movies__elements1{grid-template-columns:repeat(4, 1fr)}}@media screen and (min-width: 1368px){.movies__elements{grid-template-columns:repeat(7, 1fr)}.movies__elements--img{width:18rem}}.movie-actor{background:#dfdfdf;display:grid;padding:10px;border-radius:5%;box-shadow:2px 2px 5px #646464}.movie-actor h6{display:flex;justify-self:center;padding:5px}footer{background:#2b2b2b}.footer__links{justify-content:center;border-bottom:1px solid #545454}.footer__links a{padding:1rem}.footer__links a[name="instagram"]{color:#e1306c}.footer__links a[name="instagram"]:hover{color:#fff;transition:0.5s}.footer__links a[name="twitter"]{color:#00acee}.footer__links a[name="twitter"]:hover{color:#fff;transition:0.5s}.footer__links a[name="telegram"]{color:#0088cc}.footer__links a[name="telegram"]:hover{color:#fff;transition:0.5s}.footer__links a[name="mail"]{color:#dc483c}.footer__links a[name="mail"]:hover{color:#fff;transition:0.5s}.footer__links a[name="linkedin"]{color:#0e76a8}.footer__links a[name="linkedin"]:hover{color:#fff;transition:0.5s}.footer__links a[name="github"]{color:#484646}.footer__links a[name="github"]:hover{color:#fff;transition:0.5s}.footer__text{color:#646464;justify-content:center}.notfound{height:80vh}.notfound__title{font-family:Arial, Helvetica, sans-serif;width:100%;display:flex;justify-content:space-around;align-items:center;text-transform:uppercase}

